.main2 {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.card1,
.card2,
.card3,
.card4 {
  height: 40.68493150684932vh;
  width: 41.583333333333336vw;
  /* min-width: 312px;
  min-height: 316px; */
  border: 1px solid #ced4da;
  border-radius: 8px;
}

.card1 {
  margin-top: 7.698630136986301vh;
  margin-left: 3.510416666666667vw;
}

.card2 {
  margin-top: 7.698630136986301vh;
  margin-left: 1.5625vw;
}

.card3 {
  margin-top: 2.287671232876712vh;
  margin-left: 3.510416666666667vw;
}

.card4 {
  margin-top: 2.287671232876712vh;
  margin-left: 1.5625vw;
}

.head-box > h2 {
  font-weight: 600;
  font-size: 24px;
  line-height: 36px;
  margin-left: 1.5625vw;
}

.head-box {
  margin-top: 5.47945205479452vh;
  margin-left: 2.8645833333333335vw;
  display: flex;
  align-items: center;
}

.card1 > p,
.card2 > p,
.card3 > p,
.card4 > p {
  margin-left: 2.8645833333333335vw;
  margin-top: 3.287671232876712vh;
  font-weight: 300;
  font-size: 18px;
  line-height: 27px;
}

.card1 > button,
.card2 > button,
.card3 > button,
.card4 > button {
  width: 7.942708333333333vw;
  height: 5.47945205479452vh;
  padding: 12px, 32px, 12px, 32px;
  border: 1px solid #8064a2;
  border-radius: 8px;
  font-weight: 600;
  font-size: 14px;
  line-height: 16px;
  background-color: transparent;
  color: #8064a2;
  margin-left: 2.8645833333333335vw;
  margin-top: 4.383561643835616vh;
}

@media (max-width: 481px) {
  .main2 {
    display: flex;
    flex-direction: column;
  }

  .card1,
  .card2,
  .card3,
  .card4 {
    width: 100%;
    height: 25vh;
  }
}
